  /*  add color oerly for backgroun */
  /* Background with overlay and fixed positioning */
.bg-image-overlay {
  background-size: cover;
  background-position: center center;
  position: relative;
  height: 100vh; /* Full viewport height */
  color: white;
}

/* Overlay effect */
.bg-image-overlay::before {
  content: ''; /* Create a pseudo-element for the overlay */
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent blue overlay */
  z-index: 1; /* Ensure overlay is above the image */
}

/* Ensure the content stays above the overlay */
.bg-image-overlay .container {
  position: relative;
  z-index: 2; /* Keep text above the overlay */
}

/* Title and description styling */
h2.text-white {
  color: #fff !important;
  font-size: 3rem;
  font-weight: 700;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.6);
}

.lead.text-white {
  color: #fff !important;
  font-size: 1.25rem;
  line-height: 1.6;
